<template>
  <div id="app">
    <v-header></v-header>
    <v-tab></v-tab>
    <keep-alive>
      <router-view />
    </keep-alive>
    <v-play></v-play>
  </div>
</template>

<script>
import Header from "components/header/header";
import Tab from "components/tab/tab";
import Play from "pages/play/play";

export default {
  name: "App",
  components: {
    "v-header": Header,
    "v-tab": Tab,
    "v-play": Play
  }
};
</script>

<style lang="less">
@import "common/less/reset.less";
@import "common/less/variable.less";
#app {
  background-color: @bg_color;
}
</style>



