import Vue from 'vue'
import Vuex from 'vuex'
import { playMode } from 'store/config'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    singer: {},
    playing: true,// 播放暂停
    miniPlay: false,// 迷你播放器
    playList: [],// 播放列表
    sequence: [],// 顺序列表
    mode: playMode.sequence,// 播放模式
    currentIndex: -1// 当前播放歌曲的序号
  },
  mutations: {
    setSinger(state, singer) {
      state.singer = singer;
      console.log(singer);
    },
    setPlayList(state, playSong) {
      state.playList = playSong.songList;
      state.currentIndex = playSong.index;
      state.miniPlay = false;
    },
    setMode(state) {
      if (state.mode >= 2) {
        state.mode = 0;
      } else {
        state.mode++;
      }
    },
    miniPlay(state) {
      state.miniPlay = true;
    },
    normalShow(state) {
      state.miniPlay = false;
    },
    playSong(state, index) {
      if (index || index === 0) {
        state.currentIndex = index;
      }
      state.playing = true;
    },
    pauseSong(state) {
      state.playing = false;
    },
    nextSong(state) {
      if (state.currentIndex >= state.playList.length) {
        state.currentIndex = 0;
      } else {
        state.currentIndex++;
      }
      state.playing = true;
    },
    prevSong(state) {
      if (state.currentIndex <= 0) {
        state.currentIndex = state.playList.length;
      } else {
        state.currentIndex--;
      }
      state.playing = true;
    },
    setPlaySong(state, index) {
      if (index >= 0 && index <= (state.playList.length - 1)) {
        state.currentIndex = index;
      }
    },
    addPlaySong(state, song) {
      state.playList.unshift(song);
      state.currentIndex = 0;
      state.playing = true;
      console.log(state.playList);
    }
  },
  getters: {
    singer: store => store.singer,
    playing: store => store.playing,
    miniPlay: store => store.miniPlay,
    playList: store => store.playList,
    sequence: store => store.sequence,
    mode: store => store.mode,
    currentIndex: store => store.currentIndex
  }
});


