export function formatTime(timestamp) {
  let m = Math.floor(timestamp / 60);
  let s = Math.floor(timestamp % 60);
  return `${_padStart(m, 0)}:${_padStart(s, 0)}`;
}
function _padStart(val, str, n = 2) {
  return val.toString().padStart(n, str);
}


