 -->
<template>
  <div class="search">
    <div class="search-group">
      <i class="icon iconfont iconsousuo"></i>
      <form>
        <input v-model="searchKeyWord" type="text" placeholder="搜索..." />
      </form>
      <i @click.stop="clearKeyWord" v-show="searchKeyWord" class="icon iconfont iconguanbi"></i>
    </div>
    <div v-show="searchList.length<=0">
      <div class="hot-group">
        <h2>热门搜索</h2>
        <ul class="hot-ul">
          <li class="hot-li">莉莉安</li>
        </ul>
      </div>
      <!-- <div class="history-group">
      <h2>搜索记录</h2>
      <ul class="hot-ul">
        <li class="hot-li">莉莉安</li>
      </ul>
      </div>-->
    </div>
    <div class="search-list" v-show="searchList.length>0">
      <dl class="search-ul" v-for="(searchItem,index1) in searchList" :key="index1">
        <dt>{{searchItem.name}}</dt>
        <dd
          class="search-li"
          v-for="(item,index2) in searchItem.itemlist"
          :key="index2"
          @click="addPlaySong(item,searchItem.type)"
        >
          <i class="icon iconfont iconyinle"></i>
          <p class="song ellipse">{{item.name}} - {{item.singer}}</p>
        </dd>
      </dl>
    </div>
  </div>
</template>
<script>
import { getSearch, getSongUrl } from "api/netWork";
import { createdSinger } from "common/js/singer";
import { createdSong, initSingers } from "common/js/song";
import { ERR_CODE } from "api/config";

export default {
  data() {
    return {
      searchKeyWord: "",
      searchList: {}
    };
  },
  methods: {
    clearKeyWord() {
      this.searchKeyWord = "";
    },
    addPlaySong(item, type) {
      switch (type) {
        case 1:
          let push = {
            pic: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${item.mid}.jpg?max_age=2592000`,
            name: item.name,
            singer: item.singer,
            mid: item.mid,
            url: ""
          };
          getSongUrl(item.mid).then(res => {
            if (res.code === ERR_CODE) {
              if (res.req_0.data.midurlinfo["0"].purl) {
                push.url = `http://211.162.53.159/amobile.music.tc.qq.com/${res.req_0.data.midurlinfo["0"].purl}`;
                this.$store.commit("addPlaySong", createdSong(push));
              }
            }
          });
          break;
        case 2:
          this.$store.commit(
            "setSinger",
            createdSinger({
              id: item.id,
              mid: item.mid,
              name: item.name,
              singer: item.singer,
              pic: item.pic
            })
          );
          this.$router.push({
            path: "/singer/singerDetails"
          });
          break;
        default:
          break;
      }
    }
  },
  watch: {
    searchKeyWord() {
      if (this.searchKeyWord) {
        getSearch(this.searchKeyWord).then(res => {
          if (res.code === ERR_CODE) {
          }
          this.searchList = res.data;
          this.searchList.length = 4;
        });
      } else {
        this.searchList = { length: 0 };
      }
    }
  }
};
</script>
<style lang="less" scoped>
@import "../../common/less/variable.less";
.search {
  position: fixed;
  top: 88px;
  bottom: 0;
  width: 100%;
  padding: 0 25px;
  overflow-y: auto;
  background-color: @bg_color;
  .search-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 15px 0;
    width: 100%;
    height: 40px;
    padding: 0 10px;
    background: rgb(219, 219, 219);
    border-radius: 6px;
    form {
      flex: 1;
      input {
        width: 100%;
        text-indent: 0.6em;
        font-size: @font_size_l;
      }
    }
    .iconsousuo,
    .iconguanbi {
      font-size: 20px;
    }
  }
  .hot-group,
  .history-group {
    margin-bottom: 20px;
    h2 {
      width: 100%;
      font-size: @font_size_l;
      font-weight: bold;
    }
    .hot-ul,
    .history-ul {
      .hot-li,
      .history-li {
        margin: 6px 0;
        text-align: center;
        display: inline-block;
        padding: 0 7px;
        border-radius: 15px;
        line-height: 25px;
        height: 25px;
        color: #ccc;
        background-color: #666;
        font-size: @font_size_s;
      }
    }
  }
  .search-list {
    .search-ul {
      .search-li {
        display: flex;
        align-items: center;
        margin-bottom: 12px;
        line-height: 24px;
        .song {
          font-size: @font_size_l;
        }
        .iconyinle {
          padding: 0 10px;
          font-size: 22px;
          color: @po_color;
        }
      }
    }
  }
}
</style>


