import Vue from 'vue'
import Router from 'vue-router'
import Recommend from 'pages/recommend/recommend'
import Top from 'pages/top/top'
import Singer from 'pages/singer/singer'
import Search from 'pages/search/search'
import singerDetails from "pages/singer-details/singer-details"
import discDetails from "pages/disc-details/disc-details"

Vue.use(Router)
function getAbsolutePath() {
  let path = location.pathname;
  return path.substring(0, path.lastIndexOf('/') + 1);
}
export default new Router({
  mode: 'history',
  base: getAbsolutePath(),
  routes: [
    {
      path: '/',
      name: 'Recommend',
      component: Recommend,
      children: [
        {
          path: '/discDetails',
          name: 'discDetails',
          component: discDetails
        }
      ]
    },
    {
      path: '/top',
      name: 'Top',
      component: Top
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/singer',
      name: 'Singer',
      component: Singer,
      children: [
        {
          path: '/singer/singerDetails',
          name: 'singerDetails',
          component: singerDetails,
          beforeRouteLeave: (to, from, next) => {
            if (this.$store.getter.singer.singer_mid) {
              next();
            }
          }
        }
      ]
    }
  ]
})



