class Singer {
  constructor(params) {
    this.id = params.id;
    this.mid = params.mid;
    this.name = params.name;
    this.pic = params.pic;
    this.singer = params.singer;
  }
}
export function createdSinger(params) {
  return new Singer(params);
}


