<template>
  <div class="play-group" v-if="playList.length>0">
    <transition name="normal">
      <div class="normal" v-if="!miniPlay">
        <div class="suoxiao" @click="miniShow">
          <i class="icon iconfont iconsuoxiao"></i>
        </div>
        <div class="content">
          <div class="play-top">
            <h1 class="song ellipse">{{playList[currentIndex].name}}</h1>
            <p class="singer ellipse">{{playList[currentIndex].singer}}</p>
          </div>
          <div class="play-middle">
            <div class="album img-group">
              <img
                :class="{'rotate':playing==true}"
                :src="playList[currentIndex].pic"
                :alt="playList[currentIndex].singer"
              />
            </div>
          </div>
          <div class="play-bottom">
            <div class="progress-wrapper">
              <div class="progress-all" @click="setSongProgress">
                <div class="progress-ed" :style="'width:'+songProgress">
                  <div class="dot"></div>
                </div>
              </div>
              <div class="time">{{`${currentTime}/${duration}`}}</div>
            </div>
            <div class="operators">
              <div class="operators-i" @click="setMode">
                <i class="icon iconfont" :class="modeButtonClassName[mode]"></i>
              </div>
              <div class="operators-i" @click="prevSong">
                <i class="icon iconfont iconshangyishou1"></i>
              </div>
              <div class="operators-i" @click="playSong">
                <i class="icon iconfont" :class="playing ? 'iconGroupCopy':'iconla-'"></i>
              </div>
              <div class="operators-i" @click="nextSong">
                <i class="icon iconfont iconshangyishou1-copy"></i>
              </div>
              <div class="operators-i">
                <i class="icon iconfont iconshoucang"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mini">
      <div class="mini" @click="normalShow" v-if="miniPlay">
        <div class="img-group">
          <img
            :class="{'rotate':playing==true}"
            :src="playList[currentIndex].pic"
            :alt="playList[currentIndex].name"
          />
        </div>
        <div class="song-info">
          <h2 class="song">{{playList[currentIndex].name}}</h2>
          <p class="singer">{{playList[currentIndex].singer}}</p>
        </div>
        <div class="operators-play" @click.stop="playSong">
          <i class="icon iconfont" :class="playing ? 'iconGroupCopy':'iconla-'"></i>
        </div>
        <div class="operators-list" @click.stop="showSongListEvent">
          <i class="icon iconfont iconliebiao1"></i>
        </div>
        <transition name="slide-top">
          <div class="song-list" @click.stop v-if="showSongList">
            <ul class="list-ul">
              <li
                class="list-li"
                v-for="(song,index) in playList"
                :key="index"
                @click.stop="playCurrentSong(index)"
              >
                <p class="song">{{song.name}} - {{song.singer}}</p>
              </li>
            </ul>
          </div>
        </transition>
      </div>
    </transition>
    <audio
      ref="audio"
      :src="playList[currentIndex].url"
      @ended="modeEvent"
      @timeupdate="timeUpDate"
      autoplay
    ></audio>
  </div>
</template>
<script>
import { formatTime } from "common/js/formatTime";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      modeButtonClassName: [
        "iconiconfontdanquxunhuan2eps1",
        "iconiconfontdanquxunhuan2eps",
        "iconsuiji"
      ],
      currentTime: 0,
      duration: 0,
      songProgress: "0%",
      showSongList: false
    };
  },
  methods: {
    miniShow() {
      this.$store.commit("miniPlay");
      this.showSongList = false;
    },
    normalShow() {
      this.$store.commit("normalShow");
      this.showSongList = false;
    },
    playCurrentSong(index) {
      this.$store.commit("playSong", index);
      this.$refs.audio.play();
      this.getSongTime();
      this.showSongListEvent();
    },
    playSong() {
      if (this.playing) {
        this.$store.commit("pauseSong");
        this.$refs.audio.pause();
      } else {
        this.$store.commit("playSong");
        this.$refs.audio.play();
        this.getSongTime();
      }
    },
    nextSong() {
      this.$store.commit("nextSong");
      this.getSongTime();
    },
    prevSong() {
      this.$store.commit("prevSong");
      this.getSongTime();
    },
    getSongTime() {
      this.duration = formatTime(this.$refs.audio.duration);
    },
    setMode() {
      this.$store.commit("setMode");
    },
    modeEvent() {
      console.log(this.mode);
      if (this.mode === 0) {
        this.$store.commit("nextSong");
      }
      if (this.mode === 1) {
        this.$refs.audio.load();
      }
      if (this.mode === 2) {
        this.$store.commit(
          "setPlaySong",
          Math.floor(Math.random() * this.playList.length)
        );
      }
    },
    timeUpDate(e) {
      this.duration = formatTime(e.target.duration);
      this.currentTime = formatTime(e.target.currentTime);
    },
    setSongProgress(e) {
      let offsetWidth = e.currentTarget.getBoundingClientRect().width;
      let pro = (e.offsetX / offsetWidth) * this.$refs.audio.duration;
      this.$refs.audio.currentTime = pro;
      this.$store.commit("playSong");
      this.$refs.audio.play();
    },
    showSongListEvent() {
      this.showSongList = !this.showSongList;
    }
  },
  computed: {
    ...mapGetters([
      "singer",
      "playList",
      "miniPlay",
      "currentIndex",
      "mode",
      "playing"
    ])
  },
  watch: {
    currentTime() {
      this.songProgress =
        (this.$refs.audio.currentTime / this.$refs.audio.duration) * 100 + "%";
    }
  }
};
</script>
<style lang="less" scoped>
@import "../../common/less/variable";
.play-group {
  .normal-leave-active,
  .normal-enter-active {
    transition: all 0.4s ease;
  }
  .normal-enter, .normal-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateY(60%) scaleY(0.8);
    opacity: 0;
    filter: blur(300);
  }
  .normal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 100%;
    background-color: @bg_color;
    .suoxiao {
      position: absolute;
      z-index: 9;
      top: 10px;
      left: 15px;
      .icon {
        font-size: 24px;
        color: @Tomato_color;
        font-weight: bold;
      }
    }
    .content {
      .play-top {
        position: absolute;
        top: 0;
        width: 100%;
        text-align: center;
        .song {
          line-height: 40px;
          font-size: @font_size_lg;
        }
        .singer {
          font-size: @font_size_l;
        }
      }
      .play-middle {
        position: absolute;
        top: 80px;
        bottom: 120px;
        margin: 20px 0;
        width: 100%;
        .album {
          margin: auto;
          width: 85%;
          height: 0;
          padding-bottom: 85%;
          border-radius: 50%;
          overflow: hidden;
          background-color: rgba(158, 158, 158, 0.479);
          box-shadow: 0 0 5px rgba(102, 102, 102, 0.541);
          img {
            border-radius: 50%;
            overflow: hidden;
            max-width: none;
            max-height: none;
            width: 95%;
            height: 95%;
            box-shadow: 0 0 5px rgb(207, 207, 207);
          }
        }
      }
      .play-bottom {
        position: absolute;
        bottom: 40px;
        left: 0;
        right: 0;
        margin: auto;
        width: 80%;
        .progress-wrapper {
          width: 100%;
          display: flex;
          align-items: center;
          .progress-all {
            flex: 1;
            margin: 12px 0;
            height: 3px;
            border-radius: 10px;
            background-color: rgba(122, 0, 0, 0.5);
            box-shadow: 0 0 1px #333;
            .progress-ed {
              position: relative;
              width: 0;
              height: 100%;
              background-color: red;
              border-radius: 10px;
              .dot {
                position: absolute;
                right: -8px;
                top: 50%;
                width: 16px;
                height: 16px;
                border-radius: 50%;
                background-color: red;
                border: 2px solid rgba(251, 255, 17, 0.856);
                transform: translateY(-50%);
              }
            }
          }
          .time {
            flex: 0 0 40px;
            margin-left: 8px;
            font-size: @font_size_s;
            color: #222;
          }
        }
        .operators {
          display: flex;
          justify-content: space-around;
          align-items: center;
          .operators-i {
            .icon {
              color: @Tomato_color;
              font-size: 22px;
            }
            &:nth-child(1),
            &:nth-child(5) {
              .icon {
                font-weight: bold;
                font-size: 24px;
              }
            }
            &:nth-child(3) {
              .icon {
                font-size: 42px;
              }
            }
          }
        }
      }
    }
  }
  .mini-leave-active,
  .mini-enter-active {
    transition: all 0.6s ease;
  }
  .mini-enter,
  .mini-leave-to {
    transform: translateY(100%);
    opacity: 0;
  }
  .mini {
    position: absolute;
    bottom: -2px;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 55px;
    background-color: @bg_color;
    .img-group {
      flex-shrink: 0;
      margin: 0 15px 0 25px;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 3px #222;
    }
    .song-info {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      flex: 1;
      height: 40px;
      .song {
        color: #000;
        font-size: @font_size_m;
      }
      .singer {
        color: #666;
        font-size: @font_size_s;
      }
    }
    .operators-play,
    .operators-list {
      flex-shrink: 0;
      margin: 0 10px 0 15px;
      .icon {
        font-size: 28px;
        color: @Tomato_color;
      }
    }
    .song-list {
      position: absolute;
      bottom: 100%;
      width: 100%;
      padding: 15px 25px;
      max-height: 300px;
      overflow-y: auto;
      background-color: #fffdb6;
      .list-li {
        display: flex;
        justify-content: space-between;
        line-height: 30px;
        .song {
          font-size: @font_size_m;
          // font-weight: bold;
          color: hsl(0, 0%, 27%);
        }
      }
    }
    .slide-top-enter-active {
      transition: all 0.1s ease;
    }
    .slide-top-leave-active {
      transition: all 0.3s ease;
    }
    .slide-top-enter,
    .slide-top-leave-to {
      transform: translateY(100%);
      opacity: 0;
    }
  }
}
</style>


